@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&family=Syne+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pangolin&family=Source+Sans+Pro&family=Syne+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
}
#wrapper_front div{
  text-align: center;
  height: 50vh;
  /* width:100vw; */
  transition: all 1.2s;
  color: black;
  text-decoration: none;
  font-family: 'Pangolin', cursive;

}

#wrapper_front div:hover{
  color: #333;
  background: #ddd;
}

#wrapper_front h1{
  text-decoration: none;
  margin-top: 0;
  padding-top: 30px;


}
.names_font{
  font-family: 'Syne Mono', monospace;

}

#wrapper_front img{
  /* width:90vw; */
  height:30vh;
}

#header_button{
  background:rgb(20, 181, 230);
}

#header_button img{
  width:70px;
  color:"white";
}

.item{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  margin-top: 3rem;
}

.item div{
  display: inline-block;
}

.item img{
  height:30vh;
}

.item_detail{
  padding: 1rem;
  padding-top: 3rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.item_detail div{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width:100%;
}

.item_detail img{
  width:50px;
}

.render_down{
  opacity: 0.6;
}
